<template>
        <nav class="navbar navbar-expand-lg navbar-light">
        <div class="container-fluid fixed-header">
            <ul class="header-content map-header">
                <li>
    
                    <input v-model='store.state.map.currentFloor.name' placeholder='Give a name to this map' class="navbar-brand no-border" href="#">
        </li>
                      
                  <li class="filter-container">
                    
                    <button  class="action-map-button save-action" @click="saveMap">
       <img src="@/assets/havr-icon/Checkmark.svg"> Save change and exit
        </button>
         <button class="action-map-button cancel-action" @click="cancel">
         <img class="cancel-action-map" src="@/assets/havr-icon/cancel-action-map.png">
                
        Cancel

        </button>
                </li> 
            </ul>
            <div class="collapse navbar-collapse">
                <ul class="navbar-nav ml-auto">
                </ul>
            </div>
        </div>
    </nav>
</template>

<script>
import store from "@/flulux/store";
import mapRepository from "@/repository/map";
export default {
  data() {
    return {
      store: store
    };
  },
  methods: {
    async saveMap() {
      const updateMap = { ...this.store.state.map.currentFloor };
      const res = await mapRepository.updateFloorMap(updateMap);
      if (res) {
        this.notifyUpdate("Floor map updated with success", "success");
        return this.$router.push("Keychain-map-details");
      } else this.notifyUpdate("An error ocurred during update", "danger");
    },
    notifyUpdate(message, type) {
      this.$notify({
        message: message,
        type: type,
        horizontalAlign: "right",
        timeout: 2000,
        verticalAlign: "top"
      });
    },
    cancel() {
      this.store.actions.OPEN_GROUP_MAP_DRAWER(false);
      this.$router.push("Keychain-map-details");
    }
  }
};
</script>

<style scoped>
.filter-map {
  width: 97px;
}
.new-map {
  width: 123px;
}
.map-actions {
  width: 12px;
}
.no-border {
  border: 0px solid transparent;
}
</style>
